import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { unusedValueExportToPlacateAjd } from '@angular/core/src/render3/interfaces/injector';
const httpOptions = {
  headers: new HttpHeaders({
    //'Content-Type':  'application/json',
    //'Authorization': 'eyJjbnQiOjF9:1fqLVs:Tg_fumtlfqdbJ-bAx5S96LJ95R4',
    'ResponseType': 'json'
  })
};


@Injectable({
  providedIn: 'root'
})
export class UniService {
  serverData: JSON;
  employeeData: JSON;

  constructor(private http: HttpClient) { }

  registerNewUser(userData): Observable<any>{
    return this.http.post('http://localhost:5000/auth/login ', userData, httpOptions)
  }
  
  signIn(userData): Observable<any>{
    return this.http.post('http://localhost:5000/auth/login', userData, httpOptions)
  }
  session(): Observable<any>{
    return this.http.get('http://localhost:8000/uni/login', httpOptions)
  }
}
